.side-bar-left {
  width: 23%;
  max-width: 242px;
  float: left;
}

.side-bar-left .item {
  padding: 20px;
  ackground-color: #FFFFFF;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.3);
  margin-bottom: 15px;
}

.side-bar-left .title-sidebar {
  color: #B8B8B8;
  font-size: 10px;
  font-weight: bold;
  line-height: 14px;
}

.side-bar-left li {
  margin: 5px 0;
}

.side-bar-left ol,
.side-bar-left ul {
  margin-top: 10px;
  list-style: none;
  padding: 0;
}

.side-bar-left ul p {
  color: #3FB3F6;
  font-size: 14px;
  line-height: 19px;
}

.side-bar-left span {
  color: #B8B8B8;
  font-size: 14px;
  font-weight: 600;
  line-height: 19px;
}

.side-bar-left p {
  color: #494949;
  font-size: 14px;
  font-weight: 600;
  line-height: 19px;
  display: inline;
  padding-left: 10px;
  font-weight: normal;
  width: 185px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.side-bar-left .redeem-block p {
  position: relative;
  padding-left: 25px;
}

.side-bar-left .redeem-block p:before {
  position: absolute;
  left: 0;
  content: '';
  background: url("https://cdn4.iconfinder.com/data/icons/6x16-free-application-icons/16/Taxi.png") no-repeat;
  height: 16px;
  width: 16px;
  top: 0;
}




