@charset "UTF-8";

.course-card {
  margin-bottom: 30px;
}

.course-card .course-block {
  width: 31.33%;
  background-color: #FFFFFF;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.3);
  padding: 5px;
  margin-right: 2%;
  position: relative;
  float: left;
  margin-bottom: 2%;
}

.course-card .course-block figure {
  overflow: hidden;
}

.course-card .course-block img {
  width: 100%;
  cursor: pointer;
  -webkit-transform: scale(1);
  transform: scale(1);
  -webkit-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
}

.course-card .course-block img:hover {
  -webkit-transform: scale(1.3);
  transform: scale(1.3);
}

.course-card .course-block .info-course {
  padding: 0 5px;
}

.course-card .course-block .info-course span {
  color: #B8B8B8;
  font-size: 10px;
  font-weight: bold;
  line-height: 14px;
  display: block;
  margin: 5px 0;
}

.course-card .course-block .info-course h3 {
  color: #494949;
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
  margin-bottom: 15px;
  font-family: "Open Sans Condensed";
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.course-card .course-block .info-course a {
  display: block;
  color: #3FB3F6;
  font-size: 10px;
  font-weight: bold;
  line-height: 14px;
}

.course-card .course-block .stamp {
  position: absolute;
  top: 15px;
  left: 10px;
}

.course-card .course-block .stamp a {
  color: #FFFFFF;
  font-size: 10px;
  font-weight: bold;
  line-height: 14px;
  padding: 4px 10px 4px 20px;
  border-radius: 2px;
  background-color: #3FB34F;
}

.course-card .course-block .stamp a.start {
  padding: 4px 10px;
  background-color: #3FB3F6;
}

.course-card .course-block .stamp a.enroll:before {
  position: absolute;
  content: '✔';
  color: #fff;
  width: 6px;
  height: 6px;
  top: 0;
  left: 9px;
}

.course-card .course-block .stamp a.finish {
  background-color: #5F5F5F;
}

.course-card .course-block:nth-child(3n+3) {
  margin-right: 0;
}

figure {
  margin: 0;
}




